import './assets/styles/index.less';
import axios from 'axios';
console.log(process.env);
const API = process.env.NODE_ENV === 'development' ? '/api' : 'http://192.168.0.90:50001';

window.onload = function() {
  const next1 = document.querySelector('#next1');
  const next2 = document.querySelector('#next2');
  const prev1 = document.querySelector('#prev1');
  const close = document.querySelector('#close');
  const stepContents = document.querySelectorAll('.step-content');
  const circle = document.querySelector('.circle');
  const respText = document.querySelector('#respText');
  const locationHref = window.location.protocol+"//"+window.location.host+""+window.location.pathname;

  let loading = false;
  const STATUS_MAP = {
    '1000': '刷脸成功',
    '1001': '系统错误',
    '1003': '验证中断',
    '2002': '网络错误',
    '2003': '客户端设备时间错误',
    '2006': '刷脸失败',
    'default': '验证失败'
  }
  const SUB_CODE = {
    'Z5051': '上传刷脸图片失败。',
    'Z5052': '数据错误或程序异常。',
    'Z5053': '网络错误。',
    'Z5054': '摄像头无权限或者无法获取摄像头数据出错。',
    'Z5055': '用户退出。',
    'Z5056': '重试次数过多。',
    'default': '系统错误'
  }

  const handleResponse = (response) => {
    if (!response || Object.keys(response).length === 0) {
      return
    }
    showStep4();
    const { code, subCode } = response;
    console.log(SUB_CODE[subCode]);
    respText.innerText = STATUS_MAP[code] || STATUS_MAP['default'];
    let classStr = circle.getAttribute('class');
    if (code === 1000) {
      classStr += ' success';
    } else {
      classStr += ' fail';
    }
    circle.setAttribute('class', classStr);
  }

  const init = () => {
    const params = getRequest();
    let response;
    console.log(params);
    if (params && params.response) {
      response = JSON.parse(params.response);
      console.log(response);
      handleResponse(response);
    }
  }
  init();

  next1.addEventListener('click', async () => {
    if (loading) {
      return
    }
    const data = {};
    const metaInfo = getMetaInfo() || {};
    data.certName = document.querySelector('#certName').value;
    data.certNo = document.querySelector('#certNo').value;
    data.metaInfo = JSON.stringify(metaInfo);
    data.callback = locationHref;
    console.log(data);
    loading = true;
    const resp = await axios({
      method: 'post',
      url: API + '/Face',
      data
    });
    loading = false;
    console.log(resp);
    if (resp && resp.data && resp.data.body && resp.data.body.resultObject && resp.data.body.resultObject.certifyUrl) {
      window.location.href = resp.data.body.resultObject.certifyUrl;
    }
  })
  
  next2.addEventListener('click', showStep4);

  prev1.addEventListener('click', showStep1);
  close.addEventListener('click', () => {
    window.location.href = locationHref;
  });

  function hideAllStep() {
    for (const item of stepContents) {
      item.style.display = 'none';
    }
  }

  function showStep1() {
    hideAllStep();
    stepContents[0].style.display = 'block';
  }

  function showStep2() {
    hideAllStep();
    stepContents[1].style.display = 'block';
  }

  function showStep4() {
    hideAllStep();
    stepContents[3].style.display = 'block';
  }

  function getRequest() {
    var url = location.search; //获取url中"?"符后的字串
    var theRequest = new Object();
    if (url.indexOf("?") !== -1) {
      var str = url.substr(1);
      var strs = str.split("&");
      for(var i = 0; i < strs.length; i ++) {
        theRequest[strs[i].split("=")[0]]=unescape(strs[i].split("=")[1]);
      }
    }
    return theRequest;
  }
}